
body {
  margin: 0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

header {
  width: 100%;
  text-align: center;
  background-color:white;
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  position: relative;
  top: 0px;
  z-index: 10;
  font-family: cursive;
}
a {
  height: 60px;
  line-height: 60px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  cursor: pointer;
  font-size: 18px;
  color: black;
  text-decoration: none;
  width: 150px;
  display: inline-block;
&:hover{
    background-color: darken(white,5%);
}
}
.clear {
  clear: left;
}

#hello {
  height: 94vh;
  position: relative;
  background: linear-gradient(-45deg, rgba(246, 255, 0, .8), rgba(255, 0, 161, .8)),
  url(/vue-portfolio/static/img/sakura.6a74cf2.jpg);
  background-size: cover;
  text-align: center;
  font-family: cursive;
}
#hello h1 {
  padding-top: 30vh;
  font-size: 50px;
  color: white;
  letter-spacing: 3px;
  font-weight: normal;
  padding-right : 20px;
  padding-left : 20px;
  margin: 0;
}
#hello h2 {
  padding-top: 10vh;
  font-size: 25px;
  color: white;
}
#account {
  padding-top: 12vh;
}
#account .social-icon {
  margin: 0px 20px;
  display: inline-block;
}
#account .social-icon img {
  width: 115px;
  height: 110px;
  border-radius: 50%;
  background-color: white;
  opacity: 0.8;
}
.v-enter-active {
  -webkit-transition: opacity 1.5s;
  transition: opacity 1.5s;
}
.v-enter {
  opacity: 0;
}
@media (max-width : 670px){
#hello {
    height: 70vh;
    font-family: 'arial unicode ms';
}
#hello h1 {
    padding-top: 17vh;
    font-size: 35px;
}
#hello h2 {
    padding-top: 10vh;
    font-size: 25px;
}
}

#prof {
  height: 94vh;
  position: relative;
  color: #000044;
  text-align: center;
  font-family: 'arial unicode ms';
}
#prof table {
  padding: 0px 70px;
  font-size: 18px;
  display: inline-block;
  text-align: left;
}
#prof table th {
  padding-right: 20px;
  font-weight: normal;
}
#prof table td {
  padding : 5px 0px;
}
#prof table td a {
  margin: 0px;
  height: 25px;
  font-size: 18px;
  color: #00bfff;
  line-height: 25px;
}
#prof .heading {
  padding-top: 2vh;
  font-size: 22px;
  font-weight: bold;
}
#prof .goal {
  font-size: 18px;
}
#prof .eng {
  font-family: cursive;
}
#prof #feature {
  margin-bottom: 40px;
}
#prof #feature ul {
  width: 650px;
  margin: 0px auto;
  text-align: left;
}
#prof #feature ul li {
  margin: 15px 0px;
}
.v-enter-active {
  -webkit-transition: opacity 1.5s;
  transition: opacity 1.5s;
}
.v-enter {
  opacity: 0;
}
@media (max-width : 670px){
#prof {
    height: 70vh;
    font-family: 'arial unicode ms';
}
}

#skill {
  height: 94vh;
  color: #000044;
  font-family: cursive;
}
#skill h1 {
  padding-top: 35vh;
  font-weight: bold;
}

#development {
  height: 94vh;
  color: #000044;
  font-family: cursive;
}
#development h1 {
  padding-top: 35vh;
  font-weight: bold;
}
